import React, {Component} from "react";
import {loadModule, stopModules} from "eshop/modules/core/utils/ui";
import {connect} from "react-redux";
import ModalService from "eshop/flux/utils/OraModalService";
import MultiCartRemovePopup from "./MultiCartRemovePopup";
import {removeFromCart, showErrorMessage} from "eshop/modules/cart/actions/cart";

class MultiCartExpandingSectionComponent extends Component {

    constructor (props) {
        super(props);

    }


    componentWillMount() {
        ModalService.add((data) => <MultiCartRemovePopup id={"remove-from-cart-modal" + this.props.entry.bundleNo} bundleNo={this.props.entry.bundleNo}
            onClickRemove={this.props.removeFromCart.bind(this, null, this.props.entry.bundleNo)} />);
    }

    removeFromCart() {
        let modalId= 'remove-from-cart-modal' + this.props.entry.bundleNo;
        if (this.props.sapReservation !== '') {
            this.props.showErrorMessage(this.props.sapErrorMessage);
        } else {
            ModalService.open(modalId, {bundleNo: this.props.entry.bundleNo, offerIndex: this.props.key});
        }
    }

    render() {
        return (

                <div role="tablist" aria-multiselectable="false" ref={(ref) => this.expander = ref}>
                    <div className="opl-checkout__section opl-checkout__section--togglable u-padding is-expanded">
                      <div className="opl-checkout__section__header">
                          <div className="l-full-row u-padding-top-l">
                              <div className="l-page-row u-small-padding-left">
                                <div className="u-padding-s u-small-padding u-padding-right-l u-small-no-padding-r">
                                  <table className="u-table-fixed">
                                    <thead>
                                      <tr>
                                        <th className="u-vertical-bottom u-text-left">
                                          <table className="u-small-w-100 u-w-auto">
                                            <tbody>
                                              <tr>
                                                  <td className="u-vertical-bottom u-small-vertical-baseline u-small-w-100">
                                                      <h3 className="h2 u-inline">

                                                            { this.props.title }

                                                      </h3>
                                                  </td>
                                                  <td className="u-vertical-bottom u-padding-s u-padding-left-l" aria-hidden="true">

                                                  </td>
                                                  <td className="u-vertical-bottom u-small-vertical-baseline u-padding-s u-padding-left-l u-small-no-padding-b">
                                                    <a href="#" onClick={ this.removeFromCart.bind(this) }>
                                                        Usuń
                                                            <span className="u-acc-hide">{this.props.title}</span>
                                                    </a>
                                                  </td>
                                              </tr>
                                            </tbody>
                                          </table>
                                        </th>
                                        <th aria-hidden="true" className="u-padding-s u-padding-right-s l-col-2 u-vertical-bottom u-text-right u-small-hide">Opłaty <br className="u-small-hide u-large-hide"/>jednorazowe</th>
                                        <th aria-hidden="true" className="u-padding-s l-col-2 u-vertical-bottom u-text-right u-small-hide">Opłaty <br className="u-small-hide u-large-hide"/>miesięczne</th>
                                      </tr>
                                    </thead>
                                  </table>
                                </div>
                              </div>
                          </div>
                      </div>
                      <div aria-hidden="false" style={{display: 'block'}} className="opl-checkout__section__content" id="mod-core/modules/expander-1-tab-0" role="tabpanel" aria-labelledby="mod-core/modules/expander-1-control-0">
                        { this.props.children[0] }
                      </div>
                        { this.props.children[1] }
                    </div>
                </div>

        )
    }
}

const mapStateToProps = state => ({

});

const mapDispatchToProps = (dispatch) => ({
    removeFromCart: (data, id) => dispatch(removeFromCart(data, id)),
    showErrorMessage: (message) => dispatch(showErrorMessage(message))
});



export default connect(mapStateToProps, mapDispatchToProps)(MultiCartExpandingSectionComponent);